<template>
  <div>
    <van-nav-bar title="首页" />
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="item in bannerList" :key="item.id">
        <img :src="$preImg+item.img" alt />
      </van-swipe-item>
    </van-swipe>
    <van-tabs v-model="active" class="list">
      <van-tab title="热门推荐">
        <van-card
          v-for="item in goodsList[0].content"
          :key="item.id"
          :price="item.price"
          :title="item.goodsname"
          :thumb="$preImg+item.img"
        >
          <template #footer>
            <van-button size="mine" icon="shopping-cart-o" type="primary"  @click="add(item.id)"></van-button>
          </template>
        </van-card>
      </van-tab>

      <van-tab title="发现新品">
        <van-card
          v-for="item in goodsList[1].content"
          :key="item.id"
          :price="item.price"
          :title="item.goodsname"
          :thumb="$preImg+item.img"
        >
          <template #footer>
            <van-button size="mini" icon="shopping-cart-o" type="primary" @click="add(item.id)"></van-button>
          </template>
        </van-card>
      </van-tab>
      <van-tab title="所有商品">
        <van-card
          v-for="item in goodsList[2].content"
          :key="item.id"
          :price="item.price"
          :title="item.goodsname"
          :thumb="$preImg+item.img"
        >
          <template #footer>
            <van-button size="mini" icon="shopping-cart-o" type="primary" @click="add(item.id)"></van-button>
          </template>
        </van-card>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { getBanner, getIndexGoods,addCart } from "../utils/req";
import {mapActions,mapGetters} from "vuex";
export default {
  data() {
    return {
      active: 2,
      bannerList: [],
      goodsList: [{ content: [] }, { content: [] }, { content: [] }],
    };
  },
  computed:{
...mapGetters({
      "user":"user"
    })
  },
  methods: {
     ...mapActions({
      "reqCart":"cartListActions"
    }),
    reqBanner() {
      getBanner().then((res) => {
        this.bannerList = res.data.list;
      });
    },
    reqGoodsList() {
      getIndexGoods().then((res) => {
        this.goodsList = res.data.list;
      });
    },
    // 添加购物车
    add(goodsid){
      var data = {
        uid:this.user.uid,
        num:1,
        goodsid,
      }
      // 发起添加购物车请求
      addCart(data).then(res=>{
        // 1.提示消息添加成功
        this.$toast.success('添加购物车成功')
        // 2.更新购物车
        this.reqCart(this.user.uid)
      })
    }
  },
  mounted() {
    this.reqBanner();
    this.reqGoodsList();
  },
};
</script>

<style scoped>
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
  background-color: #39a9ed;
}
img {
  width: 100%;
  height: 100%;
}
.list{
  margin-bottom: 60px;
}

</style>


